import React, {PureComponent} from 'react';
import Label from 'r-cmui/components/Label';
import Table from 'r-cmui/components/Table';

import Demo1 from './demo1';
import Demo2 from './demo2';


class Page extends PureComponent {
    render () {
        return (
            <div className='main-container'>
                <h1 className='page-h1'>Tab </h1>
                <blockquote className='page-tip'>
                    Tab <br/>
                </blockquote>

                <h1 className='page-h1'></h1>

                <Label grid={1} className='code-col'>
                    <Label className='code-box'>
                        <Demo1 />
                    </Label>
                </Label>

                <Label grid={1} className='code-col'>
                    <Label className='code-box'>
                        <Demo2 />
                    </Label>
                </Label>

                <h2 className='page-h2'>API</h2>
                <h3 className='page-h3'>Tab</h3>

                <Table columns={[
                    {name: 'param', text: ''},
                    {name: 'desc', text: ''},
                    {name: 'type', text: ''},
                    {name: 'default', text: ''}
                ]} bordered={true} data={[
                    {param: 'className', desc: 'class', type: 'string', default: ''},
                    {param: 'style', desc: '', type: 'object', default: ''},
                    {param: 'hasClose', desc: '', type: 'boolean', default: 'false'},
                    {param: 'activeKey', desc: 'tabkey', type: 'string', default: ''},
                    {param: 'onRemove', desc: 'tab', type: 'function', default: ''}
                ]}></Table>

                <h3 className='page-h3'>Methods</h3>

                <ul className='code-methods'>
                    <li>
                        <i>getActiveKey()</i>
                        <span>key</span>
                    </li>
                </ul>


                <h3 className='page-h3'>Tab.Item</h3>

                <Table columns={[
                    {name: 'param', text: ''},
                    {name: 'desc', text: ''},
                    {name: 'type', text: ''},
                    {name: 'default', text: ''}
                ]} bordered={true} data={[
                    {param: 'className', desc: 'class', type: 'string', default: ''},
                    {param: 'style', desc: '', type: 'object', default: ''},
                    {param: 'title', desc: 'tab', type: 'string', default: ''},
                    {param: 'key', desc: 'tab', type: 'string', default: ''}
                ]}></Table>
            </div>
        );
    }
}

export default Page;



